import { IGalleryImage } from '@interfaces';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Observable, Subscription } from 'rxjs';
import { RegexUrl } from '@constants';
import { RestaurantImagesPayload } from '@payloads';
import { RestaurantService } from '@services';
import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  FormControl,
  FormBuilder,
  Validators,
  FormArray,
  FormGroup,
} from '@angular/forms';
import { GalleryImage, ResponseDetail } from '@models';

@Component({
  selector: 'app-images',
  templateUrl: './images.component.html',
  styleUrls: ['./images.component.scss'],
})
export class ImagesComponent implements OnInit, OnDestroy {
  public imagesForm = this.fb.group({
    coverImage: new FormControl(null, Validators.required),
    imageLink: new FormControl('', Validators.pattern(RegexUrl)),
    galleryImages: this.fb.array([]),
    galleryImageInput: new FormControl(),
  });
  public coverImageUrl: string;
  public galleryImages: FormArray;
  private restaurantId: number = parseInt(
    localStorage.getItem('restaurantId'),
    10
  );
  private acceptedFileTypes = ['image/jpeg', 'image/png', 'image/jpg'];
  private gallerySubscription = Subscription.EMPTY;
  private restaurantSubscription = Subscription.EMPTY;
  private galleryImageSubscription = Subscription.EMPTY;

  constructor(
    private fb: FormBuilder,
    private snackBar: MatSnackBar,
    private restaurantService: RestaurantService
  ) {}

  ngOnInit(): void {
    this.getRestaurantImages();
  }

  ngOnDestroy(): void {
    this.gallerySubscription.unsubscribe();
    this.restaurantSubscription.unsubscribe();
    this.galleryImageSubscription.unsubscribe();
  }

  /**
   * @returns Existing gallery images FormArray
   */
  public get galleryImagesAsFormArray(): FormArray {
    return this.imagesForm.get('galleryImages') as FormArray;
  }

  /**
   *  Checks if image is saved already and removes unsaved images from FormArray
   *  or deleted saved images on database
   *
   * @param imageIndex The image index FormArray
   */
  public removeGalleryImage(imageIndex: number): void {
    const clickedImage = this.galleryImagesAsFormArray.controls[imageIndex];

    if (clickedImage.get('id')) {
      this.onImageRemove(clickedImage.get('id').value);
    } else {
      this.galleryImagesAsFormArray.removeAt(imageIndex);
      this.imagesForm.get('galleryImageInput').enable();
    }
  }

  /**
   *  Saves changes. Saves matterport URL and new images only as FormData
   */
  public onSubmit(): void {
    const galleryToSave = new RestaurantImagesPayload(this.imagesForm);

    this.gallerySubscription = this.restaurantService
      .setRestaurantGallery(galleryToSave.toFormData(), this.restaurantId)
      .subscribe((response) => {
        while (this.galleryImagesAsFormArray.length > 0) {
          this.galleryImagesAsFormArray.removeAt(0);
        }
        this.getRestaurantImages();
      });
  }

  /**
   *  Checks if image is saved already and removes unsaved images from FormArray
   *  or deleted saved images on database
   *
   * @param e File Input change event
   * @param controlName Indicates FormControl name to change
   */
  public setImageValueToGallery(e: DragEvent, controlName: string): void {
    const file = e.dataTransfer
      ? e.dataTransfer.files[0]
      : (e.target as HTMLInputElement).files[0];
    const fileSize = file.size;
    const fileType = file.type;

    if (!this.acceptedFileTypes.includes(fileType)) {
      this.snackBar.open('El formato no es válido', 'Entendido', {
        duration: 15000,
      });
      return;
    }

    if (fileSize > 1048576) {
      this.snackBar.open('La imagen excede 1MB', 'Entendido', {
        duration: 15000,
      });
      this.imagesForm.get('galleryImageInput').setValue(null);
      return;
    }

    const reader = new FileReader();

    reader.onload = (eImage: any) => {
      const img = new Image();

      img.onload = () => {
        if (controlName === 'coverImage') {
          this.imagesForm.get(controlName).setValue(file);
          this.coverImageUrl = img.src;
        } else if (controlName === 'gallery') {
          if (this.galleryImagesAsFormArray.length < 10) {
            const imageAux: GalleryImage = {
              src: img.src,
              fileValue: file,
              position: this.galleryImagesAsFormArray.length + 1,
              id: null,
            };

            this.addGalleryImage(imageAux);
            if (this.galleryImagesAsFormArray.length === 10) {
              this.imagesForm.get('galleryImageInput').disable();
            }
          } else {
            this.snackBar.open(
              'Has alcanzado el limite de 10 imágenes',
              'Entendido',
              {
                duration: 15000,
              }
            );
          }
          this.imagesForm.get('galleryImageInput').setValue(null);
        }
      };
      img.src = eImage.target.result;
    };
    reader.readAsDataURL(file);
  }

  /**
   *  Adds an image to gallery images FormArray
   *
   * @param galleryImage GalleryImage object
   */
  private addGalleryImage(galleryImage: GalleryImage): void {
    this.galleryImagesAsFormArray.push(this.createGalleryImage(galleryImage));
  }

  /**
   *  Creates a FormGroup object to add the the gallery
   *  images FormArray
   *
   * @param galleryImage GalleryImage object
   * @returns FormControl object
   */
  private createGalleryImage(galleryImage: GalleryImage): FormGroup {
    return this.fb.group({
      fileValue: [galleryImage.fileValue, []],
      src: [galleryImage.src, []],
    });
  }

  /**
   *  Makes an HTTP GET request for Restaurant details including
   *  images URL's
   *
   * @param id Restaurant ID
   * @returns Response detail observable
   *  inlcluding images
   */
  private getRestaurantDetails(id: number): Observable<ResponseDetail> {
    return this.restaurantService.getRestaurantDetail(id);
  }

  /**
   *  Subscribes to Restaurant details observable, sets and patches images URL's
   */
  private getRestaurantImages(): void {
    this.restaurantSubscription = this.getRestaurantDetails(
      this.restaurantId
    ).subscribe((response: ResponseDetail) => {
      this.coverImageUrl = response.restaurant.cover.url;
      this.imagesForm.patchValue({
        imageLink: response.restaurant.matterport,
      });
      this.patchGalleryImageControls(response.restaurant.gallery);
    });
  }

  /**
   *  Makes an HTTP DELETE request to remove an image saved in the database
   *
   * @param id The ID of the image to remove
   */
  private onImageRemove(id: number): void {
    this.galleryImageSubscription = this.restaurantService
      .removeRestaurantGalleryImage(this.restaurantId, id)
      .subscribe(() => {
        while (this.galleryImagesAsFormArray.length > 0) {
          this.galleryImagesAsFormArray.removeAt(0);
        }
        this.getRestaurantImages();
      });
  }

  /**
   *  Patches gallery images FormControls with existing image URLS
   *
   * @param galleryImages Array of gallery image
   * objects coming from the server
   */
  private patchGalleryImageControls(galleryImages: Array<IGalleryImage>): void {
    galleryImages.forEach((galImg) => {
      const imageAux = this.fb.group({
        src: galImg.url,
        fileValue: null,
        id: galImg.id,
      });

      this.galleryImagesAsFormArray.push(imageAux);
    });
  }
}
